import { Component, OnInit } from '@angular/core';
import { NavigationExtras } from '@angular/router';
import { apiUrls } from '@app/shared/constants/apiUrls';
import { SVGCollection } from '@app/shared/constants/svg';
import { BroadcastService } from '@app/shared/services/broadcast.service';
import { CommonService } from '@app/shared/services/common.service';
import { HelperService } from '@app/shared/services/helper.service';
import { RequestService } from '@app/shared/services/request.service';
import { environment } from '@env/environment';
import * as XLSX from 'xlsx';

@Component({
  selector: 'app-sub-admin-list',
  templateUrl: './sub-admin-list.component.html',
  styleUrls: ['./sub-admin-list.component.css']
})
export class SubAdminListComponent implements OnInit {
  subAdminsList: Array<any> = [];
  sub_admin_id: string;
  visibleDialog: boolean;
  svgs = SVGCollection;

  constructor(
    private commonService: CommonService,
    private requestService: RequestService,
    private helperService: HelperService,
    private broadcastService: BroadcastService
  ) { }

  ngOnInit(): void {
    this.getSubAdmins();
  }

  exportexcel(): void {
    this.commonService.exportExcel('excel-table', 'sub-admin-list.xlsx');
  }

  onDelete(sub_admin_id: string) {
    this.visibleDialog = true;
    this.sub_admin_id = sub_admin_id;
  }

  getSubAdmins() {
    let url: string = environment.baseUrl + apiUrls.fetchSubAdmins;
    let params = {
      access_token: this.commonService.getSessionStorage('token', false)
    }
    this.broadcastService.handleLoader(true);
    this.requestService.get(url, params).subscribe((response: any) => {
      this.broadcastService.handleLoader(false);
      if (response.status === 200) {
        this.subAdminsList = response.data && response.data.length ? response.data : [];
        for (const admin of this.subAdminsList) {
          admin['rolesList'] = [];
          for (const key in admin.roles) {
            admin['rolesList'].push(key)
          }
        }
        this.broadcastService.handleLoader(false);
      } else {
        this.broadcastService.handleToast({ severity: 'error', summary: 'Error', detail: response.message })
      }
    }, error => {
      this.broadcastService.handleLoader(false);
      this.broadcastService.handleToast({ severity: 'error', summary: 'Error', detail: error.message })
    });
  }

  editSubAdmin(subAdmin: any) {
    const navigationExtras: NavigationExtras = {
      queryParams: { subAdmin_id: subAdmin["_id"] },
    };
    this.helperService.setObservable({
      type: 'editSubAdmin',
      data: subAdmin
    })
    this.commonService.setSessionStorage('editSubAdmin', subAdmin, true)
    this.commonService.navigate('/layout/subAdminEdit', navigationExtras);
  }

  addSubAdmin() {
    this.commonService.navigate('/layout/subAdminAdd');
  }

  deleteSubAdmin() {
    this.broadcastService.handleLoader(true);

    const payLoadObj: any = {
      access_token: this.commonService.getSessionStorage('token', false),
      sub_admin_id: this.sub_admin_id
    };
    console.log(payLoadObj);
    let url = environment.baseUrl + apiUrls.deleteSubAdmin;
    this.requestService.post(url, payLoadObj)
      .subscribe((res: any) => {
        this.broadcastService.handleLoader(false);
        if (res.status == 200) {
          this.visibleDialog = false;
          this.getSubAdmins();
        } else {
          this.broadcastService.handleToast({ severity: 'error', summary: 'Error', detail: res.message })
        }
      }, error => {
        this.broadcastService.handleLoader(false);
        this.broadcastService.handleToast({ severity: 'error', summary: 'Error', detail: error.message })

      });
  }

}
