import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { NavigationExtras } from '@angular/router';
import { apiUrls } from '@app/shared/constants/apiUrls';
import { BroadcastService } from '@app/shared/services/broadcast.service';
import { CommonService } from '@app/shared/services/common.service';
import { HelperService } from '@app/shared/services/helper.service';
import { ModalService } from '@app/shared/services/modal.service';
import { RequestService } from '@app/shared/services/request.service';
import { environment } from '@env/environment';

@Component({
  selector: 'app-product-list',
  templateUrl: './product-list.component.html',
  styleUrls: ['./product-list.component.css']
})
export class ProductListComponent implements OnInit {
  productList:any = [];
  productVariantList : any = [];
  progress = 0;
  constructor(private commonService : CommonService,private router:Router,
     private modalService : ModalService,private helperService:HelperService,
     private requestService : RequestService,private broadcastService:BroadcastService) { }

  ngOnInit(): void {
    this.getProductsData();
}


  getProductsData(){
    let url : string = environment.baseUrl + apiUrls.fetchProducts ;
    let params = {
      access_token : this.commonService.getSessionStorage('token', false)
    } 
    this.broadcastService.handleLoader(true);
   this.requestService.get(url, params)
   .subscribe((res:any)=>{
     if(res.status == 200){
      this.broadcastService.handleLoader(false);
       this.productVariantList = []
       this.productList = res.data;
       for(let i = 0;i < this.productList.length ; i++){
         for(let j =0 ; j<this.productList[i].variants.length ; j++){
          this.productVariantList.push({
            productName :this.productList[i].product_name ,
            brand : this.productList[i].brand,
            category : this.productList[i].category_name,
            mrp : this.productList[i].variants[j].mrp,
            sellerPrice : this.productList[i].variants[j].seller_price,
            sellerMargin : this.productList[i].variants[j].seller_margin,
            customerPrice : this.productList[i].variants[j].customer_price,
            customerDiscount : this.productList[i].variants[j].customer_discount,
            image : this.productList[i].variants[j].variant_img,
            sku: this.productList[i].variants[j].sku,
            moq:this.productList[i].variants[j].minimum_order,
            availStock:this.productList[i].variants[j].available_stock,
            stockSize:this.productList[i].variants[j].stock_size
          })
         }
       }
       
       console.log("product list:", this.productList, this.productVariantList);
     }

   })
  }

  


  editProduct(mode:any,data:any){
    
    const navigationExtras: NavigationExtras = {
      queryParams: { product_id: data },
    };
    this.helperService.setObservable({
      type: 'editCategory',
      data: data,
      mode:mode
    })
    this.commonService.setSessionStorage('editProduct', {data,mode}, true)
    this.commonService.navigate('/layout/product/productEdit', navigationExtras);
  }

  deleteProduct(id:any){
    let url : string = environment.baseUrl + apiUrls.deleteProduct ;
    let params = {
      access_token : this.commonService.getSessionStorage('token', false),
      product_id: id
    } 
   this.requestService.post(url, params)
   .subscribe((res:any)=>{
      debugger;
      this.getProductsData();
   })
  }

}
