import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

@Component({
  selector: 'app-seller',
  templateUrl: './seller.component.html',
  styleUrls: ['./seller.component.css']
})
export class SellerComponent implements OnInit {
  filterType: string = 'ACTIVE';

  constructor(private router: Router) { 
    // this.filterTypeChange();
  }

  filterTypeChange() {

  switch (this.filterType){
    case 'ACTIVE':{
      this.router.navigate(['/layout/seller/activeSellerList'])
      break
    }
    case 'BLOCKED':{
      this.router.navigate(['layout/seller/blockedSellerList'])
      break;
    }
    case 'PENDING':{
      this.router.navigate(['layout/seller/pendingSellerList'])
      break;
    }
    case 'DELETED':{
      this.router.navigate(['layout/seller/deleteSellerList'])
      break;
    }
  }

  }

  ngOnInit(): void {
  }

}
