  addProduct(){
    console.log(this.productAddForm.value);
    this.broadcastService.handleLoader(true);

    const payLoadObj: any = {
      ...this.productAddForm.value,
      access_token: this.commonService.getSessionStorage('token', false)
    };
    let url = environment.baseUrl + apiUrls.addProduct;
    this.requestService.post(url, payLoadObj)
      .subscribe((res: any) => {
        this.broadcastService.handleLoader(false);
        if (res.status == 200) {
          console.log('REposne is:', res);
          this.broadcastService.handleDialog({ title: '', messageHtml: `Product has been <div class="text-center">successfully added!</div>`});
          this.route.navigate(['/layout/productList','1'])
        } else {
          this.broadcastService.handleToast({ severity: 'error', summary: 'Error', detail: res.message })

        }
      }, error => {
        this.broadcastService.handleLoader(false);
        this.broadcastService.handleToast({ severity: 'error', summary: 'Error', detail: error.message })

      });

    
  }

  uploadImage(eve:any,i:number){
    
    let uploadUrl = environment.baseUrl + apiUrls.fileUPload;
    const formData = new FormData();
    formData.append('file', eve.target.files.item(0));
    this.uploadService
      .uploadFile(uploadUrl, formData)
      .pipe(
        map((res: any) => {
          return res;
        })
      )
      .subscribe((res: any) => {
        if (res.status == 200) {
        
          (<FormArray>(this.productAddForm.controls.variants))["controls"][i].get('variant_img')?.setValue(res.data)
          this.image[i]=res.data;
          console.log('message:', res.message);
        }
      });
  }

  changeSellerPrice(eve:any,i:number){
    if(+eve.value>+(<AbstractControl>((<FormArray>(this.productAddForm.get('variants')))['controls'][i].get('mrp'))).value ){
      (<AbstractControl>((<FormArray>(this.productAddForm.get('variants')))['controls'][i].get('seller_price'))).setValue(+(<AbstractControl>((<FormArray>(this.productAddForm.get('variants')))['controls'][i].get('mrp'))).value)
    }
    debugger;
  }


  
