   sellerMargin(i:number){
    let productForm=this.productAddForm.value;
    return +((((+productForm['variants'][i].mrp)-(+productForm['variants'][i].seller_price))/+productForm['variants'][i].mrp)*100).toFixed(2)
  }

  customerMargin(i:number){
    let productForm=this.productAddForm.value;
    return +((((+productForm['variants'][i].mrp)-(+productForm['variants'][i].customer_price))/+productForm['variants'][i].mrp)*100).toFixed(2)
  }

