import { HttpClient } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { apiUrls } from '@app/shared/constants/apiUrls';
import { BroadcastService } from '@app/shared/services/broadcast.service';
import { CommonService } from '@app/shared/services/common.service';
import { HelperService } from '@app/shared/services/helper.service';
import { ModalService } from '@app/shared/services/modal.service';
import { RequestService } from '@app/shared/services/request.service';
import { UploadService } from '@app/shared/services/upload.service';
import { environment } from '@env/environment';
import { map } from 'rxjs/internal/operators/map';

@Component({
  selector: 'app-brand-edit',
  templateUrl: './brand-edit.component.html',
  styleUrls: ['./brand-edit.component.css']
})
export class BrandEditComponent implements OnInit {

  categoryEditForm: FormGroup;
  categoryEditPopup: boolean = true
  categoryId: string;
  imageURL: string;
  imageName: string;
  hideImage: boolean = false;
  progress = 0;
  success: boolean = false;
  file: File;
  formData: any;
  imageUrl: string;
  fileUrl: any;
  constructor(
    private helperService: HelperService,
    private modalService: ModalService,
    private fb: FormBuilder,
    private requestService: RequestService,
    private commonService: CommonService,
    private uploadService: UploadService,
    private http: HttpClient,
    private broadcastService: BroadcastService
  ) { }

  ngOnInit(): void {
    this.buildForm();
    let storage = this.commonService.getSessionStorage('editBrand', true)
    if (storage) {
      this.formData = storage;
      this.setValue(this.formData)
    } else {
      this.customObservable();
    }
  }

  customObservable() {
    this.helperService.getObservable().subscribe((res: any) => {
      console.log('res is:', res)
      if (res.type == 'editBrand') {
        this.formData = res.data;
        this.setValue(this.formData)

      }
    })
  }

  setValue(formData: any) {
    console.log("form data is:", formData)
    this.imageUrl = formData.brand_image;
    this.categoryId = formData._id;
    this.categoryEditForm.patchValue(formData)
  }
  buildForm() {
    // , requiredFileType('png')
    this.categoryEditForm = this.fb.group({
      access_token: [this.commonService.getSessionStorage('token', false)],
      brand_image: [null],
      brand_title: [''],
      brand_description: [''],
    });
  }

  hasError(field: string, error: string) {
    const control: any = this.categoryEditForm.get(field);
    return control.dirty && control.hasError(error);
  }

  get f() {
    return this.categoryEditForm.controls;
  }

  addItem(newItem: any) {
    // this.items.push(newItem);
    this.file = newItem;

    let uploadUrl = environment.baseUrl + apiUrls.fileUPload;
    const formData = new FormData();
    formData.append('file', this.file);
    this.uploadService
      .uploadFile(uploadUrl, formData)
      .pipe(
        map((res: any) => {
          return res;
        })
      )
      .subscribe((res: any) => {
        if (res.status == 200) {
          console.log('message:', res.message);
          this.fileUrl = res.data;
        }
      });
    // console.log("File is:", this.file);
  }

  markAllAsDirty(form: FormGroup) {
    for (const control of Object.keys(form.controls)) {
      form.controls[control].markAsDirty();
    }
  }

  navigate() {
    this.commonService.navigate('/layout/categoryList')
  }
 
  onSubmit() {
    this.success = false;
    let url = environment.baseUrl + apiUrls.editBrand;
    let body = {
      access_token: this.commonService.getSessionStorage('token', false),
      brand_id: this.categoryId,
      brand_description: this.categoryEditForm.value.brand_description,
      brand_image:  this.fileUrl
    }
    this.requestService.post(url, body)
      .subscribe((res: any) => {
        if (res.status == 200) {
          console.log('REposne is:', res);
          this.broadcastService.handleDialog({ title: '', messageHtml: `Brand has been <div class="text-center">successfully updated!</div>` });
          this.navigate();
        }
      });
  }


}
