import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { apiUrls } from '@app/shared/constants/apiUrls';
import { BroadcastService } from '@app/shared/services/broadcast.service';
import { CommonService } from '@app/shared/services/common.service';
import { HelperService } from '@app/shared/services/helper.service';
import { RequestService } from '@app/shared/services/request.service';
import { environment } from '@env/environment';

@Component({
  selector: 'app-sub-admin-edit',
  templateUrl: './sub-admin-edit.component.html',
  styleUrls: ['./sub-admin-edit.component.css']
})
export class SubAdminEditComponent implements OnInit {
  subAdminForm = new FormGroup({
    'name': new FormControl('', Validators.required),
    'email': new FormControl('', [Validators.required,Validators.pattern("^[a-z0-9._%+-]+@[a-z0-9.-]+\\.[a-z]{2,4}$")]),
    'phone_no': new FormControl('', [Validators.required, Validators.minLength(10)]),
    'zone': new FormControl('', Validators.required),
  });
  formData: any;
  sub_admin_id: any;
  rolesObj: any = {};

  constructor(
    private commonService: CommonService,
    private helperService: HelperService,
    private requestService: RequestService,
    private broadcastService: BroadcastService
  ) { }

  ngOnInit(): void {
    let storage = this.commonService.getSessionStorage('editSubAdmin', true)
    if (storage) {
      this.sub_admin_id = storage._id;
      this.formData = storage;
      this.setValue(this.formData)
    } else {
      this.customObservable();
    }
  }

  setValue(formData: any) {
    console.log("form data is:", formData);
    const roles = formData.roles;
    for(const key in roles){
      this.rolesObj[key]= roles[key];
    }
    console.log(this.rolesObj)
    this.subAdminForm.patchValue(formData)
  }
  rolesChange(event: any, key: any, value: any) {
    if (event.target.checked) {
      this.rolesObj[key] = value;
    } else {
      delete this.rolesObj[key]
    }
  }
  customObservable() {
    this.helperService.getObservable().subscribe((res: any) => {
      console.log('res is:', res)
      if (res.type == 'editSubAdmin') {
        this.formData = res.data;
        this.setValue(this.formData)
      }
    })
  }

  saveSubAdmin() {
    this.broadcastService.handleLoader(true);

    const payLoadObj: any = {
      name: this.subAdminForm.controls.name.value,
      email: this.subAdminForm.controls.email.value,
      // phone_no: this.subAdminForm.controls.phone_no.value,
      zone: this.subAdminForm.controls.zone.value,
      access_token: this.commonService.getSessionStorage('token', false),
      sub_admin_id: this.sub_admin_id,
      roles: this.rolesObj
    };
    console.log(payLoadObj);
    let url = environment.baseUrl + apiUrls.editSubAdmin;
    this.requestService.post(url, payLoadObj)
      .subscribe((res: any) => {
        this.broadcastService.handleLoader(false);

        if (res.status == 200) {
          console.log('REposne is:', res);
          this.broadcastService.handleDialog({ title: '', messageHtml: `Sub Admin has been <div class="text-center">updated successfully!</div>` });

          this.commonService.navigate('/layout/subAdminList')
        } else {
          this.broadcastService.handleToast({ severity: 'error', summary: 'Error', detail: res.message })

        }
      }, error => {
        this.broadcastService.handleLoader(false);
        this.broadcastService.handleToast({ severity: 'error', summary: 'Error', detail: error.message })


      });
  }

  routeToParent() {
    this.commonService.navigate('/layout/subAdminList')
  }

}
