import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';
import { apiUrls } from '@app/shared/constants/apiUrls';
import { BroadcastService } from '@app/shared/services/broadcast.service';
import { CommonService } from '@app/shared/services/common.service';
import { HelperService } from '@app/shared/services/helper.service';
import { RequestService } from '@app/shared/services/request.service';
import { environment } from '@env/environment';

@Component({
  selector: 'app-active-seller',
  templateUrl: './active-seller.component.html',
  styleUrls: ['./active-seller.component.css']
})
export class ActiveSellerComponent implements OnInit {
  formData: any;
  seller_id: any;
  activeSellerForm = new FormGroup({
    'first_name': new FormControl(''),
    'phone_no': new FormControl(''),
    'whatsapp_no': new FormControl(''),
    'state': new FormControl(''),
    'cluster': new FormControl(''),
    'district': new FormControl(''),
    'city': new FormControl(''),
    'email': new FormControl(''),
    'store_name': new FormControl(''),
    'store_id': new FormControl(''),

  });
  pan_image_url: any;
  aadhaar_image_url: any;
  visibleDialog: boolean = false;
  dialogTitle: string;
  actionCase: string;

  constructor(
    private commonService: CommonService,
    private helperService: HelperService,
    private requestService: RequestService,
    private broadcastService: BroadcastService
  ) { }

  ngOnInit(): void {
    let storage = this.commonService.getSessionStorage('viewActiveSeller', true)
    if (storage) {
      this.seller_id = storage._id;
      this.formData = storage;
      this.setValue(this.formData)
    } else {
      this.customObservable();
    }
  }

  showDeletePopUp() {
    this.visibleDialog = true;
    this.dialogTitle = "Delete Seller";
    this.actionCase = "DELETE";
  }

  showBlockPopUp() {
    this.visibleDialog = true;
    this.dialogTitle = "Block Seller";
    this.actionCase = "BLOCK";
  }

  confirmAction(action: string) {
    let user_status;
    if (action === 'DELETE') {
      user_status = 4;
    } else if (action === 'BLOCK') {
      user_status = 3;
    }
    let url: string = environment.baseUrl + apiUrls.updateSellerStatus;
    let params = {
      access_token: this.commonService.getSessionStorage('token', false),
      seller_id: this.seller_id,
      user_status
    }
    this.requestService.post(url, params).subscribe((res: any) => {
      console.log("list resp : ", res);
      if (res.status == 200) {
        this.visibleDialog = false;
      }
    }, error => {

    });
  }

  setValue(formData: any) {
    console.log("form data is:", formData)
    this.pan_image_url = formData.pan_image_url;
    this.aadhaar_image_url = formData.aadhaar_image_url;
    this.activeSellerForm.patchValue(formData)
  }

  customObservable() {
    this.helperService.getObservable().subscribe((res: any) => {
      console.log('res is:', res)
      if (res.type == 'viewActiveSeller') {
        this.formData = res.data;
        this.setValue(this.formData)
      }
    })
  }


}
