import { Component, OnInit } from '@angular/core';
import { NavigationExtras, Router } from '@angular/router';
import { apiUrls } from '@app/shared/constants/apiUrls';
import { CommonService } from '@app/shared/services/common.service';
import { HelperService } from '@app/shared/services/helper.service';
import { RequestService } from '@app/shared/services/request.service';
import { environment } from '@env/environment';

@Component({
  selector: 'app-active-seller-list',
  templateUrl: './active-seller-list.component.html',
  styleUrls: ['./active-seller-list.component.css']
})
export class ActiveSellerListComponent implements OnInit {
  sellerData: Array<any> = [];
  filterType: string = 'ACTIVE';
  visibleDialog: boolean = false;
  dialogTitle: string;
  actionCase: string;
  sellerId: any;

  constructor(private router: Router,
    private helperService: HelperService,
    private commonService: CommonService,
    private requestService: RequestService
  ) { }

  ngOnInit(): void {
    this.getSellers();
  }

  showBlockPopUp(id: any) {
    this.sellerId = id;
    this.visibleDialog = true;
    this.dialogTitle = "Block Seller";
    this.actionCase = "BLOCK";
  }
  exportexcel(): void {
    this.commonService.exportExcel('excel-table', 'active-seller-list.xlsx');
  }

  showDeletePopUp(id: any) {
    this.sellerId = id;
    this.visibleDialog = true;
    this.dialogTitle = "Delete Seller";
    this.actionCase = "DELETE";
  }

  confirmAction(action: string) {
    let user_status;
    if (action === 'DELETE') {
      user_status = 4;
    } else if (action === 'BLOCK') {
      user_status = 3;
    }
    let url: string = environment.baseUrl + apiUrls.updateSellerStatus;
    let params = {
      access_token: this.commonService.getSessionStorage('token', false),
      seller_id: this.sellerId,
      user_status
    }
    this.requestService.post(url, params).subscribe((res: any) => {
      console.log("list resp : ", res);
      if (res.status = 200) {
        this.visibleDialog = false;
        this.getSellers();
      }
    }, error => {

    });
  }

  filterTypeChange() {

    switch (this.filterType) {
      case 'ACTIVE': {
        this.router.navigate(['/layout/seller/activeSellerList'])
        break
      }
      case 'BLOCKED': {
        this.router.navigate(['layout/seller/blockedSellerList'])
        break;
      }
      case 'PENDING': {
        this.router.navigate(['layout/seller/pendingSellerList'])
        break;
      }
      case 'DELETED': {
        this.router.navigate(['layout/seller/deleteSellerList'])
        break;
      }
    }

  }
  getSellers() {
    let url: string = environment.baseUrl + apiUrls.fetchSellers;
    let params = {
      access_token: this.commonService.getSessionStorage('token', false),
      check_type: 1
    }
    this.requestService.post(url, params).subscribe((res: any) => {
      console.log("list resp : ", res);
      if (res.status = 200) {
        this.sellerData = res.data && res.data.length ? res.data : [];
      }
    }, error => {

    });
  }

  viewActiveSeller(seller: any) {
    const navigationExtras: NavigationExtras = {
      queryParams: { seller_id: seller["_id"] },
    };
    this.helperService.setObservable({
      type: 'viewActiveSeller',
      data: seller
    })
    this.commonService.setSessionStorage('viewActiveSeller', seller, true)
    this.commonService.navigate('/layout/seller/activeSellerView', navigationExtras);
  }


}
