import { Component, OnInit } from '@angular/core';
import { apiUrls } from '@app/shared/constants/apiUrls';
import { BroadcastService } from '@app/shared/services/broadcast.service';
import { CommonService } from '@app/shared/services/common.service';
import { RequestService } from '@app/shared/services/request.service';
import { environment } from '@env/environment';

@Component({
  selector: 'app-customer-list',
  templateUrl: './customer-list.component.html',
  styleUrls: ['./customer-list.component.css']
})
export class CustomerListComponent implements OnInit {
  customersList: Array<any> = [];

  constructor(
    private commonService: CommonService,
    private requestService: RequestService,
    private broadcastService: BroadcastService
  ) { }

  ngOnInit(): void {
    this.getCustomers();
  }

  exportexcel(): void {
    this.commonService.exportExcel('excel-table','customer-list.xlsx');
  }

  getCustomers() {
    let url: string = environment.baseUrl + apiUrls.fetchCustomers;
    let params = {
      access_token: this.commonService.getSessionStorage('token', false)
    }
    this.broadcastService.handleLoader(true);
    this.requestService.get(url, params).subscribe((response: any) => {
      this.broadcastService.handleLoader(false);
      if (response.status === 200) {
        this.customersList = response.data && response.data.length ? response.data : [];
        this.broadcastService.handleLoader(false);
      } else {
        this.broadcastService.handleToast({ severity: 'error', summary: 'Error', detail: response.message })
      }
    }, error => {
      this.broadcastService.handleLoader(false);
      this.broadcastService.handleToast({ severity: 'error', summary: 'Error', detail: error.message })
    });
  }

}
