import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes } from '@angular/router';
import { AuthGuard } from '../core/guards/auth.guard';
import { DashboardComponent } from './dashboard/dashboard.component';
import { CategoryListComponent } from './category/category-list/category-list.component';
import { CategoryAddComponent } from './category/category-add/category-add.component';
import { DialogModule } from 'primeng/dialog';
import { CategoryEditComponent, EmployeeAddComponent, EmployeeListComponent } from '.';
import { EmployeeEditComponent } from './employee/employee-edit/employee-edit.component';
import { ProductListComponent } from './product/product-list/product-list.component';
import { ProductAddComponent } from './product/product-add/product-add.component';
import { ProductEditComponent } from './product/product-edit/product-edit.component';
import { OfferListComponent } from './offer/offer-list/offer-list.component';
import { OfferAddComponent } from './offer/offer-add/offer-add.component';
import { OfferEditComponent } from './offer/offer-edit/offer-edit.component';
import { CustomerListComponent } from './customer/customer-list/customer-list.component';
import { SystemSettingComponent } from './system-settings/system-setting/system-setting.component';
import { SystemSettingsEditComponent } from './system-settings/system-settings-edit/system-settings-edit.component';
import { SellerComponent } from './sellers/seller.component';
import { ActiveSellerListComponent } from './sellers/active/active-seller-list/active-seller-list.component';
import { BlockedSellerListComponent } from './sellers/blocked/blocked-seller-list/blocked-seller-list.component';
import { PendingSellerListComponent } from './sellers/pending/pending-seller-list/pending-seller-list.component';
import { SubAdminListComponent } from './sub-admin/sub-admin-list/sub-admin-list.component';
import { SubAdminAddComponent } from './sub-admin/sub-admin-add/sub-admin-add.component';
import { SubAdminEditComponent } from './sub-admin/sub-admin-edit/sub-admin-edit.component';
import { ProductComponent } from './product/product.component';
import { StockDetailComponent } from './product/stock-detail/stock-detail.component';
import { ReportComponent } from './reports/report/report.component';
import { OrderManagementComponent } from './order/order-management/order-management.component';
import { ReturnRequestComponent } from './order/return-request/return-request.component';
import { ViewReturnRequestComponent } from './order/view-return-request/view-return-request.component';
import { BrandAddComponent } from './category/brand-add/brand-add.component';
import { BrandEditComponent } from './category/brand-edit/brand-edit.component';
import { DeleteSellerListComponent } from './sellers/delete/delete-seller-list/delete-seller-list.component';
import { ActiveSellerComponent } from './sellers/view/active/active-seller/active-seller.component';
import { PendingSellerComponent } from './sellers/view/pending/pending-seller/pending-seller.component';
import { BlockedSellerComponent } from './sellers/view/blocked/blocked-seller/blocked-seller.component';
import { DeleteSellerComponent } from './sellers/view/delete/delete-seller/delete-seller.component';

const routes: Routes = [
  { path: '', redirectTo: 'dashboard', pathMatch: 'full' },
  {
    // , canActivate: [AuthGuard],
    path: 'dashboard', component: DashboardComponent,
    children: [
      // { path: '', redirectTo: 'categoryList', pathMatch: 'full' },
      { path: '', redirectTo: 'dashboard', pathMatch: 'full' },
      // { path: 'orderManagement', component: OrderManagementComponent },
      // { path: 'delieveryManagement', component: DelieveryManagementComponent },
      // { path: 'policyManagement', component: PolicyManagementComponent }
    ]
  },
  { path: 'categoryList', component: CategoryListComponent },
  { path: 'categoryAdd', component: CategoryAddComponent },
  { path: 'categoryEdit', component: CategoryEditComponent },

  { path: 'brandAdd', component: BrandAddComponent },
  { path: 'brandEdit', component: BrandEditComponent },

  { path: 'employeeList', component: EmployeeListComponent, canActivate: [AuthGuard] },
  { path: 'employeeAdd', component: EmployeeAddComponent },
  { path: 'employeeEdit', component: EmployeeEditComponent },

  { path: 'offerList', component: OfferListComponent },
  { path: 'offerAdd', component: OfferAddComponent },
  { path: 'offerEdit', component: OfferEditComponent },

  {
    path: 'product', component: ProductComponent, canActivate: [AuthGuard],
    children: [
      // { path: '', redirectTo: 'productList/:id', pathMatch: 'full' },
      { path: 'productList/:id', component: ProductListComponent },
      { path: 'productAdd', component: ProductAddComponent },
      { path: 'productEdit', component: ProductEditComponent },
      { path: 'stockDetails', component: StockDetailComponent },
    ]
  },

  { path: 'customerList', component: CustomerListComponent },

  {
    path: 'seller', component: SellerComponent,
    children: [
      { path: '', redirectTo: 'activeSellerList', pathMatch: 'full' },
      { path: 'activeSellerList', component: ActiveSellerListComponent },
      { path: 'blockedSellerList', component: BlockedSellerListComponent },
      { path: 'pendingSellerList', component: PendingSellerListComponent },
      { path: 'deleteSellerList', component: DeleteSellerListComponent },
      { path: 'activeSellerView', component: ActiveSellerComponent },
      { path: 'blockedSellerView', component: BlockedSellerComponent },
      { path: 'pendingSellerView', component: PendingSellerComponent },
      { path: 'deleteSellerView', component: DeleteSellerComponent }
    ]
  },

  { path: 'subAdminList', component: SubAdminListComponent },
  { path: 'subAdminAdd', component: SubAdminAddComponent },
  { path: 'subAdminEdit', component: SubAdminEditComponent },

  { path: 'systemSettings', component: SystemSettingComponent },
  { path: 'systemSettingsEdit', component: SystemSettingsEditComponent },

  { path: 'report', component: ReportComponent },

  { path: 'order', component: OrderManagementComponent },
  { path: 'return-order', component: ReturnRequestComponent },
  { path: 'view-return-order', component: ViewReturnRequestComponent },


];

@NgModule({
  declarations: [],
  imports: [
    CommonModule,

    RouterModule.forChild(routes),
  ],
  exports: [RouterModule]
})
export class LayoutRoutingModule { }
