import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { CommonService } from '@app/shared/services/common.service';
import { RequestService } from '@app/shared/services/request.service';
import { environment } from '@env/environment';
import { apiUrls } from '@app/shared/constants/apiUrls';
import { ActivatedRoute } from '@angular/router';
import { ModalService } from '@app/shared/services/modal.service';
import { BroadcastService } from '@app/shared/services/broadcast.service';



@Component({
  selector: 'app-employee-edit',
  templateUrl: './employee-edit.component.html',
  styleUrls: ['./employee-edit.component.css']
})
export class EmployeeEditComponent implements OnInit {
  employeeEditForm: FormGroup;
  errorMsg: string;
  _id: string | null;
  employeeId: string;
  constructor(private fb: FormBuilder,
    private route: ActivatedRoute,
    private commonService: CommonService,
    private modalService: ModalService,
    private requestService: RequestService,
    private broadcastService: BroadcastService) { }

  ngOnInit(): void {
    this.buildForm();
    this.route.queryParamMap.subscribe(queryParams => {
      this._id = queryParams.get('employee_id')
      this.getEmployee();
    })
  }

  getEmployee() {
    let employeeData = this.commonService.getSessionStorage('editEmployee', true)
    console.log("emp id:", employeeData);

    if (employeeData) {
      this.employeeId = employeeData.employee_id
      this.setValue(employeeData)
    } else {
      this.broadcastService.handleLoader(true);
      let url: string = environment.baseUrl + apiUrls.fetchOneEmployee;
      let params = {
        access_token: this.commonService.getSessionStorage('token', false),
        employee_id: this.employeeId
      }
      this.requestService.get(url, params).subscribe((res: any) => {
        if (res.status == 200) {
          this.employeeId = res.data.employee_id
          this.setValue(res.data)
        } else {
          this.broadcastService.handleToast({ severity: 'error', summary: 'Error', detail: res.message })
        }
        this.broadcastService.handleLoader(false);

      }, error => {
        this.broadcastService.handleLoader(false);

        this.broadcastService.handleToast({ severity: 'error', summary: 'Error', detail: error.message })

      })
    }

  }

  setValue(employeeData: any) {
    console.log("emp data2:", employeeData)
    this.employeeEditForm.patchValue(employeeData)
  }

  // convenience getter for easy access to form fields
  get f() {
    return this.employeeEditForm.controls;
  }

  buildForm() {
    this.employeeEditForm = this.fb.group({
      'name': ['', Validators.required],
      'email': [
        '', [Validators.required,
        Validators.pattern("^[a-z0-9._%+-]+@[a-z0-9.-]+\\.[a-z]{2,4}$")
        ]],
      'phone_no': ['', [Validators.required, Validators.minLength(10)]],
      'whatsapp': ['', [Validators.required, Validators.minLength(10)]],
      'state': ['', [Validators.required]],
      'city': ['', [Validators.required]],
      'cluster': ['', [Validators.required]],
      'district': ['', [Validators.required]]
    })
  }
  markAllAsDirty(form: FormGroup) {
    for (const control of Object.keys(form.controls)) {
      form.controls[control].markAsDirty();
    }
  }

  onSubmit() {
    if (this.employeeEditForm.invalid) {
      this.markAllAsDirty(this.employeeEditForm)
      return;
    }
    let body = {
      employee_id: this.employeeId,
      name: this.f.name.value,
      phone_no: this.f.phone_no.value,
      access_token: this.commonService.getSessionStorage('token', false)
    }
    this.broadcastService.handleLoader(true);

    let url: string = environment.baseUrl + apiUrls.editEmployee
    this.requestService.post(url, body).subscribe((res: any) => {
      console.log("response is:", res);
      this.broadcastService.handleLoader(false);
      if (res.status == 201) {
        this.errorMsg = res.message;
        this.broadcastService.handleToast({ severity: 'error', summary: 'Error', detail: res.message })
        return;
      }
      if (res.status == 200) {
        this.broadcastService.handleDialog({ title: '', messageHtml: `Invite Code has been <div class="text-center">updated successfully!</div>` });
        this.commonService.navigate('/layout/employeeList')
      } else {
        this.broadcastService.handleToast({ severity: 'error', summary: 'Error', detail: res.message })
      }
    }, (err) => {
      this.broadcastService.handleLoader(false);
      this.broadcastService.handleToast({ severity: 'error', summary: 'Error', detail: err.message })

    });


  }

  routeToParent() {
    this.commonService.navigate('/layout/employeeList')
  }
}