import { HttpEvent, HttpEventType, HttpResponse, HttpClient } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { apiUrls } from '@app/shared/constants/apiUrls';
import { BroadcastService } from '@app/shared/services/broadcast.service';
import { CommonService } from '@app/shared/services/common.service';
import { ModalService } from '@app/shared/services/modal.service';
import { RequestService } from '@app/shared/services/request.service';
import { UploadService } from '@app/shared/services/upload.service';
import { environment } from '@env/environment';
import { HelperService } from 'app/shared/services/helper.service';
import { of, pipe } from 'rxjs';
import { filter, map, switchMap, tap } from 'rxjs/operators';
import { requiredFileType } from './upload-file.validator';


@Component({
  selector: 'app-category-add',
  templateUrl: './category-add.component.html',
  styleUrls: ['./category-add.component.css']
})
export class CategoryAddComponent implements OnInit {
  categoryAddForm: FormGroup;
  categoryAddPopUp: boolean = false;
  imageURL: string;
  imageName: string;
  errorMsg: string;
  hideImage: boolean = false;
  progress = 0;
  success: boolean = false;
  file: File

  constructor(private helperService: HelperService,
    private modalService: ModalService,
    private fb: FormBuilder,
    private requestService: RequestService,
    private commonService: CommonService,
    private uploadService: UploadService,
    private http: HttpClient,
    private broadcastService: BroadcastService) { }

  ngOnInit(): void {
    // this.categoryAddPopUp = true
    // this.openModal('categoryAddPopUp')
    this.buildForm();
  }

  buildForm() {
    // , requiredFileType('png')
    this.categoryAddForm = this.fb.group({
      access_token: [this.commonService.getSessionStorage('token', false)],
      category_image: [null, [Validators.required]],
      category_title: ['', Validators.required],
      category_description: ['', Validators.required],
      // marketing_message: ['', Validators.required]
    })
  }
  navigate() {
    // this.closeModal('employeeAdd')
    this.categoryAddPopUp = false;
    this.commonService.navigate('/layout/categoryList')
  }


  get f() {
    return this.categoryAddForm.controls;
  }

  addItem(newItem: any) {
    // this.items.push(newItem);
    this.file = newItem;
    console.log("File is:", this.file);
  }
  onSubmit() {
    this.success = false;
    if (!this.categoryAddForm.valid) {
      this.markAllAsDirty(this.categoryAddForm);
      return;
    }
    let uploadUrl = environment.baseUrl + apiUrls.fileUPload;
    const formData = new FormData();
    formData.append('file', this.file);
    this.uploadService.uploadFile(uploadUrl, formData).pipe(
      switchMap((res: any) => {
        if (res.status == 200) {
          console.log("Switchmap res:", res)
          const imageFile = res.data;
          this.categoryAddForm.get('category_image')?.setValue(imageFile)
          // this.f.categoryImage.patchValue(imageFile);
          let url = environment.baseUrl + apiUrls.addCategory;
          console.log("fffffffff:", this.categoryAddForm.value)

          this.categoryAddForm.value['marketing_message'] = 'test'; // remove when offers section adds

          return this.requestService.post(url, this.categoryAddForm.value)
        } else {
          return of([])
        }
      })
    ).subscribe((res: any) => {
      this.categoryAddPopUp = true
      if (res.status == 200) {
        this.broadcastService.handleDialog({ title: '', messageHtml: `Category has been <div class="text-center">successfully added!</div>` });
        console.log("REsponse is:", res.message, this.categoryAddPopUp);
        this.commonService.navigate('/layout/categoryList')
      } else if (res.status == 201) {
        this.errorMsg = res.message
      }
    })

  }

  hasError(field: string, error: string) {
    const control: any = this.categoryAddForm.get(field);
    return control.dirty && control.hasError(error);
  }

  toFormData<T>(formValue: any) {
    const formData = new FormData();

    for (const key of Object.keys(formValue)) {
      const value = formValue[key];
      console.log("valule is:", value);
      formData.append(key, value);
    }

    return formData;
  }

  markAllAsDirty(form: FormGroup) {
    for (const control of Object.keys(form.controls)) {
      form.controls[control].markAsDirty();
    }
  }


}

