import { Component, OnInit } from '@angular/core';
import { NavigationExtras, Router } from '@angular/router';
import { apiUrls } from '@app/shared/constants/apiUrls';
import { CommonService } from '@app/shared/services/common.service';
import { HelperService } from '@app/shared/services/helper.service';
import { RequestService } from '@app/shared/services/request.service';
import { environment } from '@env/environment';

@Component({
  selector: 'app-delete-seller-list',
  templateUrl: './delete-seller-list.component.html',
  styleUrls: ['./delete-seller-list.component.css']
})
export class DeleteSellerListComponent implements OnInit {
  filterType: string = 'DELETED';
  sellerData: Array<any> = [];
  sellerId: any;

  constructor(private router: Router,
    private commonService: CommonService,
    private requestService: RequestService,
    private helperService : HelperService
  ) { }

  ngOnInit(): void {
    this.getSellers();
  }

  exportexcel(): void {
    this.commonService.exportExcel('excel-table', 'deleted-seller-list.xlsx');
  }

  filterTypeChange() {
    switch (this.filterType) {
      case 'ACTIVE': {
        this.router.navigate(['/layout/seller/activeSellerList'])
        break
      }
      case 'BLOCKED': {
        this.router.navigate(['layout/seller/blockedSellerList'])
        break;
      }
      case 'PENDING': {
        this.router.navigate(['layout/seller/pendingSellerList'])
        break;
      }
      case 'DELETED': {
        this.router.navigate(['layout/seller/deleteSellerList'])
        break;
      }
    }
  }

  getSellers() {
    let url: string = environment.baseUrl + apiUrls.fetchSellers;
    let params = {
      access_token: this.commonService.getSessionStorage('token', false),
      check_type: 4
    }
    this.requestService.post(url, params).subscribe((res: any) => {
      console.log("list resp : ", res);
      if (res.status = 200) {
        this.sellerData = res.data && res.data.length ? res.data : [];
      }
    }, error => {

    });
  }

  viewDeletedSeller(seller: any) {
    const navigationExtras: NavigationExtras = {
      queryParams: { seller_id: seller["_id"] },
    };
    this.helperService.setObservable({
      type: 'viewDeleteSeller',
      data: seller
    })
    this.commonService.setSessionStorage('viewDeleteSeller', seller, true)
    this.commonService.navigate('/layout/seller/deleteSellerView', navigationExtras);
  }


}
