import { HttpClient } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { apiUrls } from '@app/shared/constants/apiUrls';
import { BroadcastService } from '@app/shared/services/broadcast.service';
import { CommonService } from '@app/shared/services/common.service';
import { ModalService } from '@app/shared/services/modal.service';
import { RequestService } from '@app/shared/services/request.service';
import { environment } from '@env/environment';
import { Observable } from 'rxjs';

@Component({
  selector: 'app-employee-add',
  templateUrl: './employee-add.component.html',
  styleUrls: ['./employee-add.component.css']
})
export class EmployeeAddComponent implements OnInit {
  employeeAddForm: FormGroup;
  errorMsg: string;
  type=[{'code':'Employee','text':'Dsa'},{'code':'Dsa','text':'Dsa'}]
  url  = "https://raw.githubusercontent.com/sagarshirbhate/Country-State-City-Database/master/Contries.json";
  stateInfo: any[] = [];
  countryInfo: any[] = [];
  cityInfo: any[] = [];
  constructor(private fb: FormBuilder,
    private modalService: ModalService,
    private commonService: CommonService,
    private http:HttpClient,
    private requestService: RequestService,
    private broadcastService: BroadcastService) { }

  ngOnInit(): void {
    this.buildForm();
    this.getAddressData();
  }

  getAddressData(){
    this.allCountries().subscribe(country=>{
      this.countryInfo=country.Countries;
      this.onChangeCountry({value:100});
    })
    
  }

  allCountries(): Observable<any>{
    return this.http.get(this.url);
  }

  // convenience getter for easy access to form fields
  get f() {
    return this.employeeAddForm.controls;
  }

  buildForm() {
    this.employeeAddForm = this.fb.group({
      'name': ['', Validators.required],
      'email': [
        '', [Validators.required,
        Validators.pattern("^[a-z0-9._%+-]+@[a-z0-9.-]+\\.[a-z]{2,4}$")
        ]],
      'phone_no': ['', [Validators.required, Validators.minLength(10)]],
      'whatsapp': ['', [Validators.required, Validators.minLength(10)]],
      'state': ['', [Validators.required]],
      'city': ['', [Validators.required]],
      'cluster': ['', [Validators.required]],
      'district': ['', [Validators.required]]
    })
  }

  onSubmit() {
    if (this.employeeAddForm.invalid) {
      return;
    }
    let body = {
      ...this.employeeAddForm.value,
      access_token: this.commonService.getSessionStorage('token', false)
    }
    console.log("body", body);
    this.broadcastService.handleLoader(true);

    let url: string = environment.baseUrl + apiUrls.addEmployee
    this.requestService.post(url, body).subscribe((res: any) => {
      console.log("response is:", res);
      this.broadcastService.handleLoader(false);
      if (res.status == 201) {
        this.errorMsg = res.message;
        console.log("Error is:", res.message);
        this.broadcastService.handleToast({ severity: 'error', summary: 'Error', detail: res.message })
        return;
      }
      if (res.status == 200) {
        this.broadcastService.handleDialog({ title: '', messageHtml: `Invite Code has been <div class="text-center">successfully added!</div>` });
        this.commonService.navigate('/layout/employeeList');
      } else {
        this.broadcastService.handleToast({ severity: 'error', summary: 'Error', detail: res.message })
      }
    }, (err) => {
      console.log("Error is:", err);
      this.broadcastService.handleLoader(false);
      this.broadcastService.handleToast({ severity: 'error', summary: 'Error', detail: err.message })
    });
  }

  routeToParent() {
    this.commonService.navigate('/layout/employeeList')
  }

  onChangeCountry(countryValue:any) {
    this.stateInfo=this.countryInfo[countryValue.value].States;
    this.cityInfo=this.stateInfo[0].Cities;
    console.log(this.cityInfo);
  }

  onChangeState(stateValue:any) {
    this.cityInfo=this.stateInfo[stateValue.value].Cities;
    //console.log(this.cityInfo);
  }
}
