import { Component, OnInit } from '@angular/core';
import { NavigationExtras } from '@angular/router';
import { apiUrls } from '@app/shared/constants/apiUrls';
import { SVGCollection } from '@app/shared/constants/svg';
import { BroadcastService } from '@app/shared/services/broadcast.service';
import { CommonService } from '@app/shared/services/common.service';
import { ModalService } from '@app/shared/services/modal.service';
import { RequestService } from '@app/shared/services/request.service';
import { environment } from '@env/environment';

@Component({
  selector: 'app-employee-list',
  templateUrl: './employee-list.component.html',
  styleUrls: ['./employee-list.component.css']
})
export class EmployeeListComponent implements OnInit {
  employeeData: any = [];
  employeeId: string;
  visibleDialog: boolean;
  svgs = SVGCollection;
  constructor(private commonService: CommonService,
    private modalService: ModalService,
    private requestService: RequestService,
    private broadcastService: BroadcastService) { }

  ngOnInit(): void {
    this.getEmployeesData()
  }

  exportexcel(): void {
    this.commonService.exportExcel('excel-table','invite-code-list.xlsx');
  }

  getEmployeesData() {
    this.broadcastService.handleLoader(true);
    let url: string = environment.baseUrl + apiUrls.fetchEmployee;
    let params = {
      access_token: this.commonService.getSessionStorage('token', false)
    }
    this.requestService.get(url, params).subscribe((res: any) => {
      console.log("list resp : ", res);
      this.broadcastService.handleLoader(false);
      if (res.status = 200) {
        this.employeeData = res.data && res.data.length ? res.data : [];
      } else {
        this.broadcastService.handleToast({ severity: 'error', summary: 'Error', detail: res.message })
      }
    }, error => {
      this.broadcastService.handleLoader(false);
      this.broadcastService.handleToast({ severity: 'error', summary: 'Error', detail: error.message })
    })
  }

  onDelete(empId: string) {
    this.visibleDialog = true;
    this.employeeId = empId;
  }

  deleteEmployee() {
    let url: string = environment.baseUrl + apiUrls.deleteEmployee;
    this.broadcastService.handleLoader(true);
    let body = {
      access_token: this.commonService.getSessionStorage('token', false),
      employee_id: this.employeeId
    }
    this.requestService.post(url, body).subscribe((res: any) => {
      this.visibleDialog = false;
      this.broadcastService.handleLoader(false);
      if (res.status = 200) {
        this.getEmployeesData();
      } else {
        this.broadcastService.handleToast({ severity: 'error', summary: 'Error', detail: res.message })
      }
    }, error => {
      this.broadcastService.handleLoader(false);
      this.broadcastService.handleToast({ severity: 'error', summary: 'Error', detail: error.message })
    })
  }

  onEditEmployee(employee: any) {
    const navigationExtras: NavigationExtras = {
      queryParams: { employee_id: employee["_id"] },
    };
    this.commonService.setSessionStorage('editEmployee', employee, true)
    this.commonService.navigate('/layout/employeeEdit', navigationExtras);
  }

}
