import { Component, OnInit } from '@angular/core';
import { NavigationExtras } from '@angular/router';
import { apiUrls } from '@app/shared/constants/apiUrls';
import { SVGCollection } from '@app/shared/constants/svg';
import { BroadcastService } from '@app/shared/services/broadcast.service';
import { CommonService } from '@app/shared/services/common.service';
import { ModalService } from '@app/shared/services/modal.service';
import { RequestService } from '@app/shared/services/request.service';
import { environment } from '@env/environment';
import { HelperService } from 'app/shared/services/helper.service';

@Component({
  selector: 'app-category-list',
  templateUrl: './category-list.component.html',
  styleUrls: ['./category-list.component.css']
})
export class CategoryListComponent implements OnInit {
  deleteModal: boolean = false;
  categoryData: any = [];
  brandData: any = [];
  categoryId: string;
  brandId: string;
  categoryDeletePopup: boolean = false;
  svgs = SVGCollection;
  visibleDialog: boolean;
  optionType: string = 'CATEGORY';
  constructor(private helperService: HelperService,
    private commonService: CommonService,
    private modalService: ModalService,
    private requestService: RequestService,
    private broadcastService: BroadcastService) { }

  ngOnInit(): void {
    this.categoryDeletePopup = true;
    this.getCategoryData();
  }

  optionTypeChange() {
    if (this.optionType === 'CATEGORY') {
      this.getCategoryData()
    } else if (this.optionType === 'BRAND') {
      this.getBrandData();
    }
  }

  exportexcel(): void {
    this.commonService.exportExcel('excel-table', 'category-list.xlsx');
  }

  onDelete(categoryId: string) {
    if (this.optionType === 'CATEGORY') {
      this.categoryId = categoryId
    } else if (this.optionType === 'BRAND') {
      this.brandId = categoryId
    }
    this.visibleDialog = true;
  }

  addRecord() {
    if (this.optionType === 'CATEGORY') {
      this.commonService.navigate('/layout/categoryAdd');
    } else if (this.optionType === 'BRAND') {
      this.commonService.navigate('/layout/brandAdd');
    }
  }

  getBrandData() {
    let url: string = environment.baseUrl + apiUrls.fetchBrand;
    let params = {
      access_token: this.commonService.getSessionStorage('token', false)
    }
    this.broadcastService.handleLoader(true);
    this.requestService.get(url, params).subscribe((res: any) => {
      this.broadcastService.handleLoader(false);
      console.log("list resp : ", res);
      if (res.status = 200) {
        this.brandData = res.data && res.data.length ? res.data : [];
      } else {
        this.broadcastService.handleToast({ severity: 'error', summary: 'Error', detail: res.message })
      }
    }, error => {
      this.broadcastService.handleLoader(false);
      this.broadcastService.handleToast({ severity: 'error', summary: 'Error', detail: error.message })
    });
  }

  getCategoryData() {
    let url: string = environment.baseUrl + apiUrls.fetchCategory;
    let params = {
      access_token: this.commonService.getSessionStorage('token', false)
    }
    this.broadcastService.handleLoader(true);
    this.requestService.get(url, params).subscribe((res: any) => {
      this.broadcastService.handleLoader(false);
      console.log("list resp : ", res);
      if (res.status = 200) {
        this.categoryData = res.data && res.data.length ? res.data : [];
      } else {
        this.broadcastService.handleToast({ severity: 'error', summary: 'Error', detail: res.message })
      }
    }, error => {
      this.broadcastService.handleLoader(false);
      this.broadcastService.handleToast({ severity: 'error', summary: 'Error', detail: error.message })
    });
  }

  onDeleteCategory() {
    let url: any;
    let body;
    if (this.optionType === 'CATEGORY') {
      url = environment.baseUrl + apiUrls.deleteCategory;
      body = {
        access_token: this.commonService.getSessionStorage('token', false),
        category_id: this.categoryId
      }
    } else if (this.optionType === 'BRAND') {
      url = environment.baseUrl + apiUrls.deleteBrand;
      body = {
        access_token: this.commonService.getSessionStorage('token', false),
        brand_id: this.brandId
      }
    }

    this.broadcastService.handleLoader(true);
    this.requestService.post(url, body).subscribe((res: any) => {
      this.broadcastService.handleLoader(false);
      if (res.status == 200) {
        this.visibleDialog = false;
        this.optionTypeChange();
      } else {
        this.broadcastService.handleToast({ severity: 'error', summary: 'Error', detail: res.message })
      }
    }, error => {
      this.broadcastService.handleLoader(false);
      this.broadcastService.handleToast({ severity: 'error', summary: 'Error', detail: error.message })
    });
  }

  onEditCategory(category: any) {
    const navigationExtras: NavigationExtras = {
      queryParams: { category_id: category["_id"] },
    };
    this.helperService.setObservable({
      type: 'editCategory',
      data: category
    })
    this.commonService.setSessionStorage('editCategory', category, true)
    this.commonService.navigate('/layout/categoryEdit', navigationExtras);
  }

  onEditBrand(brand: any) {
    const navigationExtras: NavigationExtras = {
      queryParams: { brand_id: brand["_id"] },
    };
    this.helperService.setObservable({
      type: 'editBrand',
      data: brand
    })
    this.commonService.setSessionStorage('editBrand', brand, true)
    this.commonService.navigate('/layout/brandEdit', navigationExtras);
  }
}
